<template>
  <div class="blog-post__wrapper">
    <div class="blog-post__container">
      <h1>Creating Custom Mixins Decorator in Aurelia</h1>
      <div><span>By</span><span> Anh Nguyen</span></div>
      <h2>Mixins</h2>
      <p>
        A popular way of building up classes from reusable components is to
        build them by combining simpler partial classes. This is introduced as
        Mixins in TypeScript.
      </p>
      <p>
        For example: In our application, multiple page controllers contain data
        tables, and we’d want the page to scroll to top each time the users
        switch the data table’s page. We can create a partial class that has an
        element scrolling method and make this method available by mixing its
        class into our page controller classes as follows:
      </p>
      <pre>
        <code>
class ScrollControl {
  scrollElToY(el, y) {
    el.scrollTop = y;
  }
}

class PageWithDataTable {
  constructor() {}
  onTablePageChange() {
    this.scrollElToY(document.body, 0)
  }
}

interface PageWithDataTable extends ScrollControl {}
mixin(PageWithDataTable, [ScrollControl]);

function mixin(target: any, mixes: any[]) {
    mixes.forEach(mix => {
        Object.getOwnPropertyNames(mix.prototype).forEach(name => {
            Object.defineProperty(target.prototype, name, Object.getOwnPropertyDescriptor(mix.prototype, name));
        });
    });
}
        </code>
      </pre>
      <h2>Decorators</h2>
      <p>
        You’d be already familiar with decorators like @inject, @customElement
        in Aurelia. It is a convenient shorthand that allows us to add new or
        make changes properties/methods to a class. For example: We have a
        simple decorator hasBorder that adds a property which will get set on
        the class itself.
      </p>
      <pre>
        <code>
function hasBorder(value) {
  return function(target) {
    target.hasBorder = value;
  }
}

@hasBorder(true)
export class BigButton {

}
        </code>
      </pre>
      <pre>
        <code>
&lt;template&gt;
  &lt;button&gt; class="${hasBorder: 'has-border': ''}">Big Button&lt;/button&gt;
&lt;/template&gt;
        </code>
      </pre>
      <div>
        <em>BigButton template</em>
      </div>
      <p>
        In the example above, target is the class we are using decorator on
        (which is BigButton), and hasBorder property is added to the class.
      </p>
      <h2>Creating Aurelia Custom Mixins Decorator</h2>
      <p>
        Combine it all together, we can write a mixins decorator that takes
        partial classes as parameters and mixes them in with the target class.
      </p>
      <pre>
        <code>
//my-custom-mixin.ts
export default function myCustomMixin(...params) {
  const mixins = Array.from(arguments);
    return function(target) {
      mixins.forEach(mixin => {
        Object.getOwnPropertyNames(mixin.prototype).forEach(name => {
          if (name === 'constructor') {
            return;
          }
          Object.defineProperty(target.prototype, name, Object.getOwnPropertyDescriptor(mixin.prototype, name));
        });
      });
    };
}
        </code>
      </pre>
      <div>
        <em>Custom Mixin Decorator</em>
      </div>
      <pre>
        <code>
//scroll-control.ts
import zenscroll from 'zenscroll';

export class ScrollControl {
    scrollToY(destination, duration?, cb?) {
        zenscroll.toY(destination, duration, cb);
    }
}
        </code>
      </pre>
      <div>
        <em>Scroll control mixin class</em>
      </div>
      <pre>
        <code>
import myCustomMixin from 'mixins/my-custom-mixin';
import {ScrollControl} from 'mixins/scroll-control';

@myCustomMixin(ScrollControl)
export class PageController {
  onPageChange() {
    this.scrollToY(0, 200, () => {
      console.log('scrolled to 0');
    });
  }
}

export interface PageController extends ScrollControl {}
        </code>
      </pre>
      <div>
        <em>Page controller class that consumes scrolling method</em>
      </div>
      <h2>Conclusion</h2>
      <p>
        In this article, we've scraped the surface of Mixins in Typescript and
        Javascript Decorators by utilizing both to create a custom mixin
        decorator in Aurelia. Hopefully it can save you some times in Aurelia
        application development process.
      </p>
      <p>For further reading on decorator and mixins:</p>
      <ul>
        <li>
          <a
            href="https://www.sitepoint.com/javascript-decorators-what-they-are/"
            target="_blank"
            >Javascript Decorators: What They Are and When to Use Them by Graham
            Cox</a
          >
        </li>
        <li>
          <a href="https://www.typescriptlang.org/docs/handbook/mixins.html"
            >Typescript Mixins</a
          >
        </li>
      </ul>
    </div>
  </div>
</template>

